.contact {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.contact-click-style {
  min-height: calc(80vh - 9.5rem);
}

.contact h2 {
  margin: 2rem 0;
  font-size: 34px;
  font-family: "Poppins", sans-serif;
  font-weight: 600;
}

.form {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5rem 1rem;
  background-color: #edf4f6;
  border-radius: 5px;
}

.form-text {
  width: 40%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.form-text h2 {
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  font-size: 27px;
  line-height: 40.5px;
  margin-bottom: 2rem;
  color: #000;
}

.form-text p {
  font-size: 17px;
  line-height: 25.5px;
  color: #484848;
}

.form-box {
  width: 50%;
  text-align: right;
}

.form-box form label {
  width: 100%;
}

.form-box form button,
.form-box form input {
  width: 65%;
  height: 61px;
  border: none;
  margin: 1rem;
  border-radius: 5px;
  outline-color: #226500;
}

.form-box form input::placeholder {
  color: #656566;
  font-size: 19px;
}

.form-box form input {
  padding-left: 1.6rem;
  color: #656566;
  font-size: 19px;
}

.form-box form button {
  background-color: #226500;
  color: #fff;
  font-size: 22px;
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.15s linear;
}

.form-box form button:hover {
  background-color: #2b7c02;
  transform: translateY(-4px);
}

.form-box form button:active {
  transform: translateY(-2px);
  background-color: #226500;
}

@media screen and (max-width: 767px) {
  .form {
    flex-direction: column;
    align-items: center;
    padding: 2rem;
  }

  .contact h2 {
    text-align: center;
  }

  .form-text h2 {
    font-size: 23px;
    line-height: 35px;
    text-align: center;
  }

  .form-text p {
    font-size: 16px;
    line-height: 24px;
    text-align: center;
  }

  .form-box form button,
  .form-box form input {
    width: 100%;
    height: 50px;
  }

  .form-box,
  .form-text {
    width: 90%;
  }
}
